
.custom-button {
    background-color: #007bff; 
    color: #fff; 
    padding: 10px 20px; 
    border: none; 
    border-radius: 5px; 
    font-size: 16px; 
    cursor: pointer; 
    transition: background-color 0.3s ease;
  }
  
  
  .custom-button:hover {
    background-color: #0056b3; 
  }
  